@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');
body {
    font-family: 'Inter', sans-serif;
}
.container {
    max-width: 1280px;
    padding: 0 32px;
    margin: 0 auto;
}
.d-flex {
    display: flex;
}
main {
    max-width: 1440px;
    margin: 0 auto;
}
img {
    max-width: 100%;
}
.header{
    &__block {
        align-items: center;
        height: 80px;
        .brand {
            max-width: 142px;
            margin-right: 40px;
        }
        .nav-list {
            .nav-item {
                font-style: normal;
                font-weight: 500;
                font-size: 16px;
                line-height: 24px;
                color: #667085;
                text-decoration: none;
                margin-right: 32px;
                &:hover {
                    text-decoration: underline;
                }
                
            }
        }
    }
}
.head {
    &__block {
        padding-left: 7.78%;
        align-items: center;
        justify-content: space-between;
    }
    &__text {
        h1.page__title {
            font-style: normal;
            font-weight: 500;
            font-size: 60px;
            line-height: 72px;
            letter-spacing: -0.02em;
            color: #101828;
            max-width: 540px;
            margin-bottom: 24px;
        }
        p.page__subtitle {
            font-weight: 400;
            font-size: 20px;
            line-height: 30px;
            color: #667085;
            max-width: 480px;
        }
    }
    &__img {
        width: 50%;
    }
}
.contact {
    background: #F9FAFB;
    padding: 96px 0 160px;
    &__block {
        justify-content: space-between;
    }
    &__box {
        width: 50%;
        padding-right: 20px;
        h4 {
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            color: #6941C6;
            margin-bottom: 12px;
        }
        h2 {
            font-weight: 600;
            font-size: 36px;
            line-height: 44px;
            letter-spacing: -0.02em;
            color: #101828;
            margin-bottom: 20px;
        }
        p.contact__subtitle {
            font-weight: 400;
            font-size: 20px;
            line-height: 30px;
            color: #667085;
        }
        .box {
            display: flex;
            margin-bottom: 48px;
            &:last-child {
                margin-bottom: 0;
            }
            img.icon {
                height: 20px;
                width: 20px;
                margin-right: 20px;
            }
            .text {
                h3 {
                    font-weight: 500;
                    font-size: 20px;
                    line-height: 30px;
                    color: #101828;
                    margin-bottom: 8px;
                }
                a {
                    display: block;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #667085;
                    text-decoration: none;
                }
            }

        }
    }
}
.img {
    margin-top: -96px;
    margin-bottom: 96px;
    img {
        width: 100%;
    }
}

.footer {
    background: #1D2939;
    padding: 64px 0 48px;
    .brand {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 64px;
        img {
            max-width: 142px;
            width: 100%;
            
        }
    }
    &__box {
        justify-content: space-between;
        align-items: center;
        color: #98A2B3;
        padding-top: 32px;
        border-top: 1px solid #475467;
        .copy {
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
        }
        .nav-list {
            align-items: center;
            list-style: none;
            .nav-item {
                margin-left: 16px;
                .nav-link {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #98A2B3;
                    text-decoration: none;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }

        }
    }
}
@media (max-width: 1280px) {
    .head__block {
        padding-left: 32px;
    }
}
@media (max-width: 990px) {
    .head__block {
        padding-left: 20px;
    }
    .container {
        padding: 0 20px;
    }
    .head__text h1.page__title {
        font-size: 48px;
        line-height: 120%;
    }
}
@media (max-width: 768px) {
    .head__block {
        flex-wrap: wrap;
        justify-content: center;
        text-align: center;
        padding-right: 20px;
    }
    .head__text {
        margin-bottom: 20px;
    }
    .head__text p.page__subtitle {
        margin: 0 auto;
    }
    .contact__block {
        flex-wrap: wrap;
    }
    .contact {
        padding-top: 40px;
    }
    .contact__box {
        width: 100%;
        margin-bottom: 48px;
        &:first-child {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
    .header__block {
        justify-content: space-between;
    }
    
}
@media (max-width: 500px) {
    .head__img {
        width: 100%;
    }
    .contact__box {
        padding-right: 0;
    }
    .footer__box {
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .footer__box .nav-list {
        margin-left: 0;
        padding: 0;
        order: 1;
    }
    .footer__box .copy {
        order: 2;
        margin-top: 20px;
    }
    .head__text h1.page__title {
        font-size: 36px;
    }
    .head__text p.page__subtitle {
        font-size: 16px;
        line-height: 20px;
    }
    .header__block .nav-list .nav-item {
        &:last-child {
            margin-right: 0;
        }
    }
    .img {
        margin-bottom: 40px;
    }
}
@media (max-width: 389px) {
    .header__block {
        height: auto;
        padding: 20px 0;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .header__block .brand {
        margin-right: 0;
        margin-bottom: 20px;
    }
}